import React from "react";
import Link from "next/link";
import { useMutation, useQueryClient } from "@tanstack/react-query";
import { Trash2 } from "lucide-react";

import { RouterOutputs } from "@acme/api";
import { Button } from "@acme/ui/button";
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@acme/ui/card";
import { toast } from "@acme/ui/toast";

import { useTRPC } from "~/trpc/react";
import { getCDNUrl } from "~/utils/cdn";

export const VideoCard = (props: {
  video: RouterOutputs["video"]["all"][number];
}) => {
  const { video } = props;

  const trpc = useTRPC();
  const queryClient = useQueryClient();
  const deletePost = useMutation(
    trpc.video.delete.mutationOptions({
      onSuccess: async () => {
        await queryClient.invalidateQueries(trpc.video.pathFilter());
      },
      onError: (err) => {
        toast.error(
          err.data?.code === "UNAUTHORIZED"
            ? "You must be logged in to delete a post"
            : "Failed to delete post",
        );
      },
    }),
  );

  return (
    <Card className="overflow-hidden transition-all hover:shadow-lg">
      <Link href={`/video/${video.id}`} className="block">
        <CardHeader className="pb-2">
          <CardTitle className="text-2xl font-bold text-primary">
            {video.title}
          </CardTitle>
        </CardHeader>

        <CardContent className="p-0">
          <div className="relative">
            <video
              className="h-64 w-full object-cover"
              src={getCDNUrl(`video/${video.id}`)}
            />
            <div className="absolute inset-0 flex items-center justify-center bg-black/5 transition-colors hover:bg-black/10">
              <div className="rounded-full bg-white/90 px-4 py-2 font-medium text-primary shadow-sm">
                View Details
              </div>
            </div>
          </div>
        </CardContent>
      </Link>

      <CardFooter className="flex justify-between p-4 pt-2">
        <Link
          href={`/video/${video.id}`}
          className="text-sm text-muted-foreground hover:text-primary"
        >
          View Comments
        </Link>
        <Button
          variant="ghost"
          size="sm"
          className="text-sm font-medium text-destructive hover:bg-destructive/10"
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();
            deletePost.mutate(video.id);
          }}
        >
          <Trash2 className="mr-1 h-4 w-4" />
          Delete
        </Button>
      </CardFooter>
    </Card>
  );
};
