"use client";

import React, { useCallback, useState } from "react";
import { useMutation } from "@tanstack/react-query";
import { useDropzone } from "react-dropzone";

import { cn } from "@acme/ui";
import { Button } from "@acme/ui/button";
import { Progress } from "@acme/ui/progress";
import { toast } from "@acme/ui/toast";

import type { UploadState } from "~/hooks/useMultipartUpload";
import { useMultipartUpload } from "~/hooks/useMultipartUpload";
import { useTRPC } from "~/trpc/react";

interface VideoDropzoneProps {
  onUploadComplete?: (videoId: string) => void;
  className?: string;
}

export function VideoDropzone({
  onUploadComplete,
  className,
}: VideoDropzoneProps) {
  const trpc = useTRPC();
  const { mutateAsync: createVideo } = useMutation(
    trpc.video.create.mutationOptions(),
  );

  const [uploadState, setUploadState] = useState<UploadState>({
    uploaded: 0,
    total: 0,
    percentage: 0,
    error: null,
  });
  const [isUploading, setIsUploading] = useState(false);
  const { uploadFile } = useMultipartUpload();

  const onDrop = useCallback(
    async (acceptedFiles: File[]) => {
      const file = acceptedFiles[0];

      if (!file) return;

      // Validate file type
      if (!file.type.startsWith("video/")) {
        toast.error("Please upload a video file");
        return;
      }

      try {
        setIsUploading(true);
        // Generate a unique ID for this video

        const newVideoId = await createVideo({
          title: file.name,
        });

        const id = newVideoId[0]?.id;

        if (!id) {
          throw new Error("Failed to create video");
        }

        // Start upload
        const uploadedVideoId = await uploadFile(file, {
          videoId: id,
          onProgress: (state) => {
            setUploadState(state);
            if (state.error) {
              toast.error(`Upload failed: ${state.error}`);
              setIsUploading(false);
            }
          },
          onError: (error) => {
            toast.error(`Upload error: ${error.message}`);
            setIsUploading(false);
          },
        });

        toast.success("Video uploaded successfully!");
        onUploadComplete?.(uploadedVideoId);
        setIsUploading(false);
      } catch (error) {
        // Error is already handled by onError callback
        setIsUploading(false);
      }
    },
    [uploadFile, onUploadComplete],
  );

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      "video/*": [],
    },
    multiple: false,
    disabled: isUploading,
  });

  return (
    <div className={cn("w-full", className)}>
      <div
        {...getRootProps()}
        className={cn(
          "flex w-full cursor-pointer flex-col items-center justify-center rounded-lg border-2 border-dashed p-6 transition-colors",
          isDragActive
            ? "border-primary bg-primary/10"
            : "border-muted-foreground/30",
          isUploading && "pointer-events-none opacity-60",
        )}
      >
        <input {...getInputProps()} />

        {isUploading ? (
          <div className="w-full space-y-4">
            <div className="mb-1 flex items-center justify-between">
              <p className="text-sm font-medium">Uploading video...</p>
              <span className="text-sm">
                {uploadState.percentage.toFixed(0)}%
              </span>
            </div>
            <Progress value={uploadState.percentage} className="h-2 w-full" />
            <p className="text-xs text-muted-foreground">
              {(uploadState.uploaded / (1024 * 1024)).toFixed(2)} MB of{" "}
              {(uploadState.total / (1024 * 1024)).toFixed(2)} MB
            </p>
          </div>
        ) : (
          <>
            <div className="mb-4">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
                className="h-12 w-12 text-muted-foreground"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M12 9v6m3-3H9m12 0a9 9 0 1 1-18 0 9 9 0 0 1 18 0Z"
                />
              </svg>
            </div>
            <p className="mb-2 text-sm font-medium">
              {isDragActive
                ? "Drop the video here"
                : "Drag and drop a video file, or click to browse"}
            </p>
            <p className="text-xs text-muted-foreground">
              Supports most video formats (MP4, MOV, AVI, etc.)
            </p>
            <Button variant="outline" className="mt-4">
              Select Video
            </Button>
          </>
        )}
      </div>

      {uploadState.error && (
        <div className="mt-4 rounded border border-destructive bg-destructive/10 p-3 text-sm text-destructive">
          {uploadState.error}
        </div>
      )}
    </div>
  );
}
